import React, {Component} from "react";
import PropTypes from 'prop-types';
import {integer, fraction} from "../../../../../magnum2/components/Utils";
import {DesktopPriceBox, MobilePriceBox} from "eshop/modules/cart/components/entriesList/shared/PriceBoxes";

const initialProps = {
    monthlyPrices: [],
    onlyFirst: false
};

const MonthlyPrices = (props = initialProps) => {
        let monthlyPrices = props.monthlyPrices;
        if (!monthlyPrices || monthlyPrices.length==0) {
            return (<span className="h4 u-no-spacing">—</span>);
        }

        let firstMonthPrice = monthlyPrices[0];
        return (
            <div>
                <span className="u-acc-hide">Opłaty miesięczne</span>
                <span className="h2 u-no-spacing">{integer(firstMonthPrice.gross)}</span>
                <span className="h4 u-no-spacing">,{fraction(firstMonthPrice.gross)}&nbsp;{firstMonthPrice.currency}</span>
                {props.onlyFirst ? null : renderRemainingMonthlyPrices(monthlyPrices)}
            </div>
        );

};

const renderRemainingMonthlyPrices = (monthlyPrices) => {
        return (
            <div>
                {
                    monthlyPrices.map((monthlyPrice, index) => {
                        //Skip the first one
                        if (index == 0) {
                            return null;
                        }

                        let price = monthlyPrice.gross.split(',');
                        return (
                            <span key={monthlyPrice.monthFrom + "-" + monthlyPrice.monthTo} className="u-right u-text-nowrap">
                                od&nbsp;{monthlyPrice.monthFrom} {monthlyPrice.monthTo? "do " + monthlyPrice.monthTo + " ": ""}m-ca {monthlyPrice.gross} {monthlyPrice.currency}
                            </span>
                        );
                    })
                }
            </div>
        );
    }

export default MonthlyPrices;